body {
  background-image: url(background.png);
  background-image: url(background-movies.png);
  background-repeat: repeat;
  height: 100vh;

}

.fade-enter{
   opacity: 0;
}
.fade-exit{
   opacity: 1;
}
.fade-enter-active{
   opacity: 1;
}
.fade-exit-active{
   opacity: 0;
}
.fade-enter-active,
.fade-exit-active{
   transition: opacity 500ms;
}

.red-enter{
}
.red-exit{
   -webkit-filter: url(#red);
   filter: url(#red);
}
.red-enter-active{
   -webkit-filter: url(#red);
   filter: url(#red);
}
.red-exit-active{
}
.red-enter-active,
.red-exit-active{
   transition: 500ms;
}

svg {
  display: none;
}

.red {
   -webkit-filter: url(#red);
   filter: url(#red);
}


.response-text {
  padding-top: 5vh;
  padding-left: 5vw;
  padding-right: 5vw;
}

.emojis {
  justify-content: center;
  display: flex;
  font-size:10vw;
  height: 100%;
  width: 100%;
  position: fixed;
  align-items: center;
}



#buttons {
  text-align: right;
  padding: 1vh;
}

#buttons > button {
 margin-right: 1vh;
 font-size: 2vh;
}


@media screen and (orientation: portrait) {

  #buttons {
    padding: 1vw;
  }

  #buttons > button {
   margin-right: 1vw;
   font-size: 2vw;
  }
}

